body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer-container {
  background-color: #414f36;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  margin-bottom: 24px;
  padding: 24px;
  color: #fff;
}

.footer-subscription > p {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.footer-subscription-heading {
  margin-bottom: 24px;
  font-size: 24px;
}

.footer-subscription-text {
  margin-bottom: 24px;
  font-size: 20px;
}

.footer-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.footer-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.footer-link-wrapper {
  display: flex;
}

.footer-link-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.footer-link-items h2 {
  margin-bottom: 16px;
}

.footer-link-items > h2 {
  color: #fff;
}

.footer-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-input::-webkit-input-placeholder {
  color: #b1b1b1;
}

.footer-input::placeholder {
  color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
  color: #fff;
  font-size: 24px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
#logo-image {
  width: 60px;
  height: 60px;
  display: inline-block;
  border-radius: 50%;
}
.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}

@media screen and (max-width: 768px) {
}

.size {
  padding: 1px;
}

.productCardContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 70px 0;
}

.position {
  position: relative;
}

.topContainer {
  background-color: #71886d9e;
  width: 100%;
  height: 60px;
  position: fixed;
  left: 0;
  z-index: 3;
  padding-bottom: 5px;
}
.topHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: auto 25px;
  align-items: center;
}
.logo-image {
  width: 42px;
  height: 42px;
  display: inline-block;
  position: relative;
  top: -5px;
  border-radius: 50%;
}
.name {
  display: inline-block;
  position: relative;
  top: -15px;
}
.rightIcon1 {
  margin-right: 5px;
}
.rightIcon2 {
  position: relative;
  top: 6px;
}
.rightClicks {
  display: flex;
  flex-direction: row;
}

.ProductItem {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: 25px;
}

.ProductItem .imageContainer img {
  width: 275px;
  height: 286px;
  position: relative;
  z-index: -2;
  object-fit: cover;
}
.styleBorder {
  height: 286px;
  width: 275px;
  position: relative;
  margin-top: -258px;
  margin-right: auto;
  margin-left: 21px;
  border: 7px solid #5b6b3e;
  z-index: 2;
}
h4 {
  font-size: 30px;
  margin: 25px;
  font-weight: 700;
  color: #5b6b3e;
}
p {
  font-size: 16px;
  line-height: 1.5rem;
  margin: 10px 25px;
}

.ProductItem:nth-child(even) {
  flex-direction: column-reverse;
}

.productContainer {
  display: flex;
  margin: 25px;
  justify-content: space-evenly;
}
h1 {
  font-size: 80px;
  text-align: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  color: #5b6b3e;
}

.homepage {
  width: 100%;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.homeContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.25),
      rgba(0, 0, 0, 0.25)
    ),
    url(/vaishnadpk/homegreen/static/media/h-min.cc7386a9.jpeg);
  background-position: 50% 50%;
  background-size: cover;
  min-height: 500px;
}
.homeContainer h1 {
  font-family: Oswald, sans-serif;
  font-weight: 400;
  margin: 60px 0 0 391px;
  font-size: 55px;
  color: #fff;
}
.homeContainer button {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
  background-image: none;
  border-style: none;
  align-self: flex-end;
  padding: 19px 47px 20px;
  margin: 20px 50px auto;
  border-radius: 30px;
}


